import React from "react";
import { Form, FormSpy } from "react-final-form";

import List from "../List";
import Box from "../Box";
import Text from "../Text";
import Field from "../Field";
import Dropdown from "../Dropdown";
import Checkbox from "../Checkbox";
import Button from "../Button";

import { required, focusOnErrors } from "../../../utils/form";
import useOnMount from "../../../utils/useOnMount";
import useLocalStorage from "../../../utils/useLocalStorage";
import { LS_FORM_USER_DATA_KEY } from "../../../utils/constants";
import StatesDropdown from "./StatesDropdown";

const getFormId = (formEl) => formEl.id.replace("mktoForm_", "");

const persistedFields = [
  "FirstName",
  "LastName",
  "Email",
  "Company",
  "Title",
  "Country",
  "State",
  "Industry",
];

export default function MarketoForm({
  onSuccess,
  submitLabel = "Subscribe",
  codeSnippet = `
  <script src="https://www10.contino.io/js/forms2/js/forms2.min.js"></script>   
  <form id="mktoForm_1305"></form>   
  <script>
   MktoForms2.setOptions({formXDPath : "/rs/964-BIK-421/images/marketo-xdframe-relative.html"});
   MktoForms2.loadForm("//www10.contino.io", "964-BIK-421", 1305);
  </script>
  <script id="teknklFormsPlus-base-1.0.3" src="//cdpn-js.figureone.com/teknkl-formsplus-1.0.3.min.js"></script>
  <script id="teknklFormsPlus-purl-1.0.0" src="//cdpn-js.figureone.com/teknkl-formsplus-purl-1.0.0.min.js"></script>
  <script>FormsPlus.pURL.fixSession();</script>
`,
  children,
  ...otherProps
}) {
  const elRef = React.useRef();
  const snippetRef = React.useRef();
  const [id, setId] = React.useState();
  const [initialValues, setInitialValues] = React.useState();
  const [persistedData, setPersistentData] = useLocalStorage(
    LS_FORM_USER_DATA_KEY
  );
  console.log("persistedData: ", persistedData);
  const [formConfig, setFormConfig] = React.useState();

  const handleFormLoaded = React.useCallback(
    (formId) => {
      console.log("DBG: Getting country and industry");
      const countryEl = document.querySelector("#Country");
      const industryEl = document.querySelector("#Industry");

      if (!countryEl || !industryEl) return;

      console.log("DBG: Got country and industry");
      setFormConfig((prevState) => ({
        ...prevState,
        country: {
          options: [...countryEl.options],
        },
        industry: {
          options: [...industryEl.options],
        },
      }));

      console.log("DBG: State is set");
      const form = window.MktoForms2.getForm(formId);

      console.log("DBG: Got form");
      if (persistedData) {
        console.log("DBG: Has persisted data");
        form.setValues(JSON.parse(persistedData));
      }
    },
    [persistedData]
  );

  const handleMarketoLoaded = React.useCallback(() => {
    const snippetEl = snippetRef.current;
    const formEl = snippetEl.querySelector("form");
    const formId = getFormId(formEl);

    // if form is already initialized, we stop here
    if (formEl.children.length) {
      console.log("DBG: Is initialized");
      handleFormLoaded(formId);
      return;
    } else {
      console.log("DBG: Initializing");
      const script = snippetEl.querySelector("script:not([src])");

      const injectScript = document.createElement("script");
      injectScript.text = script.innerText;
      snippetEl.appendChild(injectScript);

      window.MktoForms2.whenReady(() => handleFormLoaded(formId));
    }
  }, [handleFormLoaded]);

  // Load Marketo code snippet
  useOnMount(() => {
    if (id) return;

    console.log("DBG: Has no ID", id);
    let injectScript;
    let marketoLoading;
    snippetRef.current = document.createElement("div");
    const snippetEl = snippetRef.current;
    snippetEl.style.display = "none";
    snippetEl.innerHTML = codeSnippet;
    const formEl = snippetEl.querySelector("form");
    const formId = getFormId(formEl);
    const script = snippetEl.querySelector("script[src]");
    elRef.current.appendChild(snippetEl);

    injectScript = document.createElement("script");

    setId(formId);

    const handleLoaded = () => {
      handleMarketoLoaded(formId);
    };

    const loadMarketo = () => {
      console.log("DBG: Loading Marketo");
      if (!window.MktoForms2) {
        console.log("DBG: No MktoForms2 found");
        injectScript.addEventListener("load", handleLoaded);
        injectScript.setAttribute("src", script.src);
        snippetEl.appendChild(injectScript);
      } else {
        handleLoaded();
      }
    };

    if (document.readyState === "loading") {
      console.log("DBG: Is loading");
      document.addEventListener("DOMContentLoaded", () => {
        marketoLoading = setTimeout(loadMarketo, window.MktoForms2 ? 0 : 1500);
      });
    } else {
      marketoLoading = setTimeout(loadMarketo, window.MktoForms2 ? 0 : 1500);
    }

    return () => {
      const elsToRemove = [];
      elsToRemove.push(
        document.querySelector("form.mktoForm"),
        document.querySelector("#mktoStyleLoaded"),
        snippetRef.current
      );
      elsToRemove.forEach((el) => el && el.remove());
      injectScript.removeEventListener("load", handleLoaded);
      clearTimeout(marketoLoading);
    };
  }, []);

  const handleSubmit = (formData) => {
    return new Promise((resolve, reject) => {
      const form = window.MktoForms2.getForm(id);
      const persistedValues = persistedFields.reduce((acc, curr) => {
        acc[curr] = formData[curr];
        return acc;
      }, {});

      form.onSuccess(() => {
        resolve();
        onSuccess && onSuccess();
        setPersistentData(persistedValues);
        return false;
      });
      // form.on
      //   .then((val) => {
      //     console.log(val);
      //     return val;
      //   })
      //   .catch((_error) => ({ [FORM_ERROR]: "Request Failed" }));
      form.setValues(formData);
      form.submit();
    });
  };

  // Trigger a re-render for persistent data otherwise this doesn't work with SSR rehydration
  React.useEffect(() => {
    console.log("DBG: persistedData: ", persistedData);
    persistedData && setInitialValues(JSON.parse(persistedData));
  }, [persistedData]);

  return (
    <Form
      decorators={[focusOnErrors]}
      onSubmit={handleSubmit}
      initialValues={initialValues}
    >
      {({ handleSubmit, submitting, values }) => (
        <Box ref={elRef} {...otherProps}>
          <form onSubmit={handleSubmit}>
            <List className="fields" gap={[2, 3]}>
              <Field
                name="FirstName"
                id="FirstName_clone"
                placeholder="First Name"
                validate={required}
                defaultValue=""
              />
              <Field
                name="LastName"
                id="LastName_clone"
                placeholder="Last Name"
                validate={required}
              />
              <Field
                name="Email"
                id="Email_clone"
                placeholder="Email Address"
                type="email"
                validate={required}
              />
              <Field
                name="Company"
                id="Company_clone"
                placeholder="Company"
                validate={required}
              />
              <Field
                name="Title"
                id="Title_clone"
                placeholder="Job Title"
                validate={required}
              />
              <Field
                component={Dropdown}
                name="Country"
                id="Country_clone"
                placeholder="Select Country"
                validate={required}
              >
                {formConfig &&
                  formConfig.country.options.map((option) => (
                    <option value={option.value} key={option.value}>
                      {option.label}
                    </option>
                  ))}
              </Field>
              <FormSpy
                subscription={{ values }}
                formId={id}
                component={StatesDropdown}
              />
              <Field
                component={Dropdown}
                name="Industry"
                id="Industry_clone"
                placeholder="Select Industry"
                validate={required}
              >
                {formConfig &&
                  formConfig.industry.options.map((option) => (
                    <option value={option.value} key={option.value}>
                      {option.label}
                    </option>
                  ))}
              </Field>
              {children}
              <Text as="div" sx={{ fontSize: 1, color: "white" }}>
                <Checkbox
                  name="HasOptedIntoEmail__c"
                  label="I would like to receive marketing communications regarding Contino news, services and events."
                  sx={{ mb: 2 }}
                />
                You may unsubscribe from these communications at any time. For
                more information on how to unsubscribe, our privacy practices,
                and how we are committed to protecting and respecting your
                privacy, please review Privacy Policy.
              </Text>
            </List>
            <Button
              className="mktoButton"
              type="submit"
              disabled={submitting || !formConfig}
              sx={{
                mt: [2, 3],
                width: "100%",
              }}
            >
              {submitLabel}
            </Button>
          </form>
        </Box>
      )}
    </Form>
  );
}
