import React from "react";

import Box from "../Box";

import { ReactComponent as DownArrowIcon } from "../../../images/icons/down-arrow.svg";

const InnerSelectDisclosure = React.forwardRef(
  ({ variant, placement, ...otherProps }, ref) => (
    <Box
      ref={ref}
      variant={`forms.input.${variant}`}
      sx={{
        position: "relative",
        display: "flex",
        width: "100%",
        cursor: "pointer",
        alignItems: "center",
        border: 0,
        background: "none",
        p: 0,
      }}
      {...otherProps}
    />
  )
);

export default function Dropdown({
  placeholder,
  input,
  id,
  variant = "default",
  options = [],
  sx,
  ...otherProps
}) {
  return (
    <InnerSelectDisclosure
      as="label"
      htmlFor={id || input.name}
      variant={variant}
      aria-label={placeholder}
    >
      {/* <Box as="label" htmlFor={id || input.name} sx={visuallyHidden}>
        {placeholder}
      </Box> */}
      <Box
        as="select"
        placeholder={placeholder}
        variant={`forms.input.${variant}`}
        sx={{
          position: "absolute",
          top: 0,
          left: 0,
          width: "100%",
          height: "100%",
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
          flexGrow: 1,
          option: {
            fontSize: "body",
            color: "text",
          },
          "&:focus + .arrow-icon": {
            opacity: 1,
          },
          ...sx,
        }}
        style={{
          color:
            input.value === "" ? "rgba(255, 255, 255, 0.5)" : "currentColor",
        }}
        id={id || input.name}
        name={input.name}
        {...input}
        value={input.value || placeholder}
        {...otherProps}
      />
      <Box
        className="arrow-icon"
        sx={{
          ml: "auto",
          mr: 3,
          color: "currentColor",
          opacity: 0.4,
        }}
      >
        <DownArrowIcon width="1em" height="1em" />
      </Box>
    </InnerSelectDisclosure>
  );
}
