import React from "react";

import Field from "../Field";
import Dropdown from "../Dropdown";

import { required } from "../../../utils/form";

export default function StatesDropdown({ values, formId }) {
  const [options, setOptions] = React.useState([]);

  React.useEffect(() => {
    if (!window.MktoForms2) return;

    const form = window.MktoForms2.getForm(formId);

    if (!form) return;

    form.setValues({ Country: values.Country });
    const state = document.querySelector("#State");

    if (state) {
      setOptions([...document.querySelector("#State").options]);
    } else {
      setOptions([]);
    }
  }, [values.Country, formId]);

  return (
    !!options.length && (
      <Field
        component={Dropdown}
        name="State"
        id="_State"
        placeholder="Select State"
        validate={required}
      >
        {options.map((option) => (
          <option value={option.value} key={option.value}>
            {option.label}
          </option>
        ))}
      </Field>
    )
  );
}
