import React from "react";
import { Field as RFField } from "react-final-form";

import Box from "../Box";
import { hiddenInput } from "../../../utils/styles";

export default function Field({
  name,
  id,
  value,
  type = "checkbox",
  variant = "default",
  label,
  validate,
  sx,
  ...otherProps
}) {
  return (
    <RFField name={name} type={type} value={value} validate={validate}>
      {({ input, meta: { pristine, error, active } }) => {
        return (
          <Box sx={{ display: "inline-flex", ...sx }}>
            <Box
              as="input"
              type={type}
              id={name || id}
              sx={{ ...hiddenInput }}
              {...input}
              {...otherProps}
            />
            <Box
              as="label"
              htmlFor={name || id}
              variant="forms.input.default"
              sx={{
                position: "relative",
                width: [17, 17],
                height: [17, 17],
                borderRadius: ["5px", "5px"],
                p: 0,
                flexShrink: 0,
                "&:after": {
                  position: "absolute",
                  content: "' '",
                  display: input.checked ? "block" : "none",
                  borderRadius: "circle",
                  width: "7px",
                  height: "7px",
                  bg: "currentColor",
                  margin: "auto",
                  top: 0,
                  bottom: 0,
                  left: 0,
                  right: 0,
                },
              }}
            />
            {label && (
              <Box sx={{ display: "inline-flex", ml: 2, mt: "-1px" }}>
                <Box as="label" htmlFor={name || id}>
                  {label}
                </Box>
              </Box>
            )}
          </Box>
        );
      }}
    </RFField>
  );
}
